@import "../../../scss/variables";
@import "../../../scss/mixins";

.navigation {
  width: 20vw;
  transition: all 0.4s ease;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  background-image: linear-gradient(120deg, $primary-color-dark, $secondary-color-purple);

  .topContainer {
    .storeName {
      font-family: "Pacifico", cursive;
      text-align: center;
      font-size: 3rem;
      padding: 1rem;
      color: white;
      &::after {
        @include underlineAfter;
      }
    }

    .btnMenu {
      display: none;
      width: 4rem;
      height: 4rem;
      border: none;
      outline: none;
      border-radius: 3px;
      background-color: transparent;
      .barsAnimated {
        transition: all 0.3s ease;
        display: block;
        width: 2.5rem;
        height: 3px;
        background-color: white;
        margin: auto;
        position: relative;
        &::after {
          content: '';
          transition: all 0.3s ease;
          display: block;
          width: 2.5rem;
          height: 3px;
          background-color: white;
          position: absolute;
          top: 0.8rem;
        }
        &::before {
          content: '';
          transition: all 0.3s ease;
          display: block;
          width: 2.5rem;
          height: 3px;
          background-color: white;
          position: absolute;
          bottom: 0.8rem;
        }
      }
      .crossAnimated {
        background-color: transparent;
        &::after {
          top: 0;
          transform: rotateZ(135deg);
        }
        &::before {
          bottom: 0;
          transform: rotateZ(-135deg);
        }
      }
      .color{
        background-color: #f77737;
      }
    }
  }
  .bottomContainer {
    

    .socials {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 3rem 2rem;
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-evenly;
  
      &Link {
        text-decoration: none;
        font-size: 3rem;
        width: 4rem;
        height: 4rem;
        transition: all 0.3s ease;
        background-color: white;
        position: relative;
        border-radius: 5px 10px;
  
        i {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
  
      .facebook {
        color: #3b5998;
  
        &:hover {
          color: white;
          background-color: #3b5998;
        }
      }
  
      .instagram {
        color: #f77737;
  
        &:hover {
          color: white;
          background-color: #f77737;
        }
      }
  
      .whatsapp {
        color: #4ac959;
  
        &:hover {
          color: white;
          background-color: #4ac959;
        }
      }

      .tiktok {
        color: 	#010101;

        &:hover {
          color: white;
          background-color: 	#010101;
        }
      }
    }
  }
}

@media screen and (max-width: 1358px) {
  .navigation {
    width: 25vw;
  }
}

@media screen and (max-width: 1000px) {
  .navigation {
    width: 100vw;
    height: 6rem;
    top: 0;
    left: 0;
    right: 0;
    bottom: initial;

    &.showNavigation {
      height: 100vh;
    }

    .topContainer {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;
      padding: 0 3rem;
      
      .storeName {
        padding: 0;
        &::after {
          display: none;
        }
      }

      .btnMenu {
        display: block;
      }
    }
    .growContainer {
      height: auto;
      visibility: visible;
    }
    .bottomContainer {
      height: 0;
      visibility: hidden;

      &.showBottomcontainer {
        height: initial;
        visibility: visible;
      }
    }
  }
}

@media screen and (max-width:620px) {
  .navigation {
    .topContainer{
      padding: 1rem 1.5rem;
      .storeName {
        font-size: 2.3rem;
      }
    }
  }
}
